import React from 'react';
import './index.css';

function index(props) {
    const { onClose, visible, handleUpdate } = props;
    return (
        <div
            className="modal-cart"
            style={
                visible === true
                    ? {
                          visibility: 'visible',
                          transform: 'translate(0,0)',
                          transitionDelay: '0s, 0s, 0.3s',
                      }
                    : { visibility: 'hidden' }
            }
        >
            <div className="modal-cart-header">
                <span>GIỎ HÀNG CỦA BẠN (ĐANG CÓ 8 SẢN PHẨM)</span>
                <button
                    type="button"
                    onClick={() => {
                        onClose();
                    }}
                >
                    <i className="fa fa-times" />
                </button>
            </div>
            <div className="modal-cart-body">
                <table>
                    <thead>
                        <tr>
                            <th />
                            <th>Sản phẩm</th>
                            <th>Đơn giá</th>
                            <th>Số lượng</th>
                            <th>Thành tiền</th>
                        </tr>
                    </thead>
                    <tbody>{props.children}</tbody>
                </table>
                <div className="total-price">
                    <span>Tổng: 24,000,000đ</span>
                </div>
            </div>
            <div className="modal-cart-footer">
                <div className="actions-cart">
                    <button
                        type="button"
                        id="update-cart"
                        onClick={handleUpdate}
                    >
                        <span>CẬP NHẬT GIỎ HÀNG</span>
                    </button>
                    <button type="button" id="payment">
                        <span>TIẾN HÀNH THANH TOÁN</span>
                    </button>
                </div>
            </div>
        </div>
    );
}

export default index;
