import {
    fork,
    take,
    call,
    put,
    delay,
    takeLatest,
    select,
    takeEvery,
} from 'redux-saga/effects';
import {
    fetchProductSuccess,
    fetchKindProductsSuccess,
} from '../actions/products';
import { fetchBlogsSuccess } from '../actions/blogs';
import { addCartSuccess, updateCartSuccess } from '../actions/carts';
import * as typeProducts from '../const/products';
import * as typeBlogs from '../const/blogs';
import * as typeCarts from '../const/carts';
import { getProduct, getKindProducts } from '../apis/products';
import { getBlogs } from '../apis/blogs';
import { STATUS_CODE } from '../const/index';

function* fetchKindProductsSaga({ payload }) {
    const { kind } = payload;
    const resp = yield call(getKindProducts, kind);
    const { status, data } = resp;
    if (status === STATUS_CODE.SUCCESS) {
        yield put(fetchKindProductsSuccess(data));
    }
}

function* fetchProductSaga({ payload }) {
    const { kind, id } = payload;
    const resp = yield call(getProduct, { kind, id });
    const { status, data } = resp;
    if (status === STATUS_CODE.SUCCESS) {
        yield put(fetchProductSuccess(data));
    }
}

function* fetchBlogsSaga() {
    yield delay(500);
    const resp = yield call(getBlogs);
    const { status, data } = resp;
    if (status === STATUS_CODE.SUCCESS) {
        yield put(fetchBlogsSuccess(data));
    }
}

function* addCartSaga({ payload }) {
    const { product } = payload;
    const productCart = yield select((state) => state.carts.cart);
    let cloneProductCart = [...productCart];
    var test = [];
    yield productCart.map((item) => {
        if (item.id) {
            test.push(item.id);
        }
    });
    let index = test.indexOf(product.id);
    if (index !== -1) {
        cloneProductCart[index].quantity += product.quantity;
        yield put(addCartSuccess(cloneProductCart));
    } else {
        yield cloneProductCart.push(product);
        yield put(addCartSuccess(cloneProductCart));
    }
    /* var test;
    if (productCart.length > 0) {
        test = productCart.filter((item) => {
            return item.id === product.id;
        });
    } */
    /*  yield put(addCartSuccess(product)); */
}

function* updateCartSaga({ payload }) {
    const { product } = payload;
    const productCart = yield select((state) => state.carts.cart);
    let cloneProductCart = [...productCart];
    var test = [];
    yield productCart.map((item) => {
        if (item.id) {
            test.push(item.id);
        }
    });
    let index = test.indexOf(product.id);
    if (index !== -1) {
        cloneProductCart[index].quantity = product.quantity;
        yield put(updateCartSuccess(cloneProductCart));
    }
}

function* rootSaga() {
    yield takeEvery(typeProducts.FETCH_KIND_PRODUCTS, fetchKindProductsSaga);
    yield takeEvery(typeProducts.FETCH_PRODUCT, fetchProductSaga);
    yield takeEvery(typeBlogs.FETCH_BLOG, fetchBlogsSaga);
    yield takeEvery(typeCarts.ADD_CART, addCartSaga);
    yield takeEvery(typeCarts.UPDATE_CART, updateCartSaga);
}

export default rootSaga;
