import * as productsConstants from '../const/products';

export const fetchKindProducts = (kind) => {
    return {
        type: productsConstants.FETCH_KIND_PRODUCTS,
        payload: { kind },
    };
};

export const fetchKindProductsSuccess = (data) => {
    return {
        type: productsConstants.FETCH_KIND_PRODUCTS_SUCCESS,
        payload: { data },
    };
};

export const fetchProduct = (kind, id) => {
    return {
        type: productsConstants.FETCH_PRODUCT,
        payload: { kind, id },
    };
};

export const fetchProductSuccess = (data) => {
    return {
        type: productsConstants.FETCH_PRODUCT_SUCCESS,
        payload: { data },
    };
};
