import * as cartsConstants from '../const/carts';

const initialState = {
    cart: [],
};

const reducer = (state = initialState, action) => {
    switch (action.type) {
        case cartsConstants.ADD_CART_SUCCESS: {
            const { cart } = action.payload;
            return {
                ...state,
                cart,
            };
        }
        case cartsConstants.UPDATE_CART_SUCCESS: {
            const { cart } = action.payload;
            return {
                ...state,
                cart,
            };
        }
        default:
            return state;
    }
};

export default reducer;
