import React, { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import Products from '../../components/Body/ProductsPage/Products/index';
import LinkProduct from '../../components/Body/LinkTitle/index';
import Product from '../../components/Body/ProductsPage/Product/index';
import Pagination from '../../components/Body/Pagination/index';
import './index.css';
import { fetchKindProducts, destroyProducts } from '../../actions/products';

function ProductsPage(props) {
    const products = useSelector((state) => state.products.products);
    const dispatch = useDispatch();
    const [param, setParam] = useState({
        title: '',
        id: null,
    });
    const [pagination, setPagination] = useState({
        page: 1,
        limit: 8,
        totals: 9,
    });
    const [firstNumber, setFirstNumber] = useState(0);
    const [newLimit, setNewLimit] = useState(8);
    const { params } = props.match.params;

    useEffect(() => {
        switch (params) {
            case 'all-products':
                dispatch(fetchKindProducts(0));
                setParam({
                    title: 'toàn bộ sản phẩm',
                    id: 0,
                });
                break;
            case 'vest-cong-so':
                dispatch(fetchKindProducts(1));
                setParam({
                    title: 'vest công sở',
                    id: 1,
                });
                break;
            case 'vest-cuoi':
                dispatch(fetchKindProducts(2));
                setParam({
                    title: 'vest cưới',
                    id: 2,
                });
                break;
            case 'so-mi-nam':
                dispatch(fetchKindProducts(3));
                setParam({
                    title: 'sơ mi nam',
                    id: 3,
                });
                break;
            case 'ao-phong-nam':
                dispatch(fetchKindProducts(4));
                setParam({
                    title: 'áo phông nam',
                    id: 4,
                });
                break;
            case 'giay-da-nam':
                dispatch(fetchKindProducts(5));
                setParam({
                    title: 'giày da nam',
                    id: 5,
                });
                break;
            case 'quan-au-nam':
                dispatch(fetchKindProducts(6));
                setParam({
                    title: 'quần âu nam',
                    id: 6,
                });
                break;
            default:
                setParam({
                    title: '',
                    id: null,
                });
                break;
        }
        return () => {
            setPagination({
                page: 1,
                limit: 8,
                totals: 9,
            });
            setFirstNumber(0);
            setNewLimit(8);
        };
    }, [...params]);

    useEffect(() => {
        let totals = products.length;
        setPagination({
            ...pagination,
            totals,
        });
    }, [products]);

    const showProduct = (products) => {
        let result = null;
        let productsLocal = [];
        if (param.id !== null) {
            if (products.length > 0) {
                for (let i = firstNumber; i < firstNumber + newLimit; i++) {
                    productsLocal.push(products[i]);
                }
            }
            result = productsLocal.map((product, index) => {
                if (product) {
                    return <Product key={index} product={product} />;
                }
            });
        } else {
            result = (
                <React.Fragment>
                    <h1>NOT FOUND</h1>
                </React.Fragment>
            );
        }
        return result;
    };

    const onPageChange = (newPage, newLimit) => {
        const { limit } = pagination;
        let firstNumber = (newPage - 1) * limit;
        setFirstNumber(firstNumber);
        setNewLimit(newLimit);
        let page = newPage;
        setPagination({ ...pagination, page });
    };

    return (
        <div id="products-body">
            <LinkProduct title={param.title} />
            <Products title={param.title}>{showProduct(products)}</Products>
            <Pagination pagination={pagination} onPageChange={onPageChange} />
        </div>
    );
}

export default ProductsPage;
