import React from 'react';
import { useState } from 'react';
import { useEffect } from 'react';
import './index.css';
import { convertPrice } from '../../../../common/convertPrice';

function ProductCart(props) {
    const { product, handleChangeQuantity } = props;
    const [quantity, setQuantity] = useState(0);

    useEffect(() => {
        setQuantity(product.quantity);
    }, [product.quantity]);

    const changeQuantity = (e) => {
        setQuantity(e.target.value);
        handleChangeQuantity(product, e.target.value);
    };

    const showTotalPrice = (price, quantity) => {
        let totalPrice = parseInt(price) * parseInt(quantity);
        return convertPrice(totalPrice.toString());
    };

    const showProductCart = (product) => {
        let result = null;
        if (product) {
            result = (
                <tr>
                    <td>
                        <div
                            id="imgProductModal"
                            style={{
                                backgroundImage: `url(${require('../../../../img/products/' +
                                    product.img)})`,
                            }}
                        ></div>
                    </td>
                    <td>{product.type.toUpperCase()}</td>
                    <td>
                        {convertPrice(product.price)}
                        <u>đ</u>
                    </td>
                    <td>
                        <input
                            type="text"
                            value={quantity}
                            onChange={changeQuantity}
                        />
                    </td>
                    <td>
                        {showTotalPrice(product.price, product.quantity)}
                        <u>đ</u>
                    </td>
                </tr>
            );
        }
        return result;
    };

    return <React.Fragment>{showProductCart(product)}</React.Fragment>;
}

export default ProductCart;
