import React, { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import queryString from 'query-string';
import { fetchProduct } from '../../actions/products';
import { addCart, updateCart } from '../../actions/carts';
import Product from '../../components/Body/ProductPage/index';
import ModalCart from '../../components/Body/ProductPage/ModalCart/index';
import ProductCart from '../../components/Body/ProductPage/ProductCart/index';

function ProductPage(props) {
    const product = useSelector((state) => state.products.products);
    const cart = useSelector((state) => state.carts.cart);
    const dispatch = useDispatch();
    const [showModal, setShowModal] = useState(false);
    const [newProduct, setNewProduct] = useState({});
    const search = queryString.parse(props.location.search);
    let { kind } = search;
    let { id } = search;

    useEffect(() => {
        dispatch(fetchProduct(kind, id));
    }, []);

    const handleAddCart = (product, quantity, size) => {
        setShowModal(true);

        let productCarts = { ...product, quantity: parseInt(quantity), size };
        dispatch(addCart(productCarts));
    };

    const onClose = () => {
        setShowModal(false);
    };

    const handleChangeQuantity = (product, newQuantity) => {
        /* console.log({ ...product, quantity: parseInt(newQuantity) }); */
        /* dispatch(updateCart({ ...product, quantity: parseInt(newQuantity) })); */
        setNewProduct({ ...product, quantity: parseInt(newQuantity) });
    };

    const handleUpdate = () => {
        setShowModal(false);
        dispatch(updateCart(newProduct));
        setTimeout(() => {
            setShowModal(true);
        }, 400);
    };

    const showProductCart = () => {
        let result = null;
        if (cart) {
            result = cart.map((item, index) => {
                return (
                    <ProductCart
                        key={index}
                        product={item}
                        handleChangeQuantity={handleChangeQuantity}
                    />
                );
            });
        }
        return result;
    };

    return (
        <React.Fragment>
            <Product handleAddCart={handleAddCart} product={product[0]}>
                <ModalCart
                    visible={showModal}
                    onClose={onClose}
                    handleUpdate={handleUpdate}
                >
                    {showProductCart()}
                </ModalCart>
            </Product>
        </React.Fragment>
    );
}

export default ProductPage;
