import axiosService from '../common/axiosService';
import { API_ENDPOINT } from '../const/index';

const url = 'products';

/* export const getProducts = () => {
    return axiosService.get(`${API_ENDPOINT}/${url}`);
};
 */
export const getKindProducts = (kind) => {
    if (kind === 0) return axiosService.get(`${API_ENDPOINT}/${url}`);
    else return axiosService.get(`${API_ENDPOINT}/${url}?kind=${kind}`);
};

export const getProduct = (param) => {
    return axiosService.get(
        `${API_ENDPOINT}/${url}?kind=${param.kind}&id=${param.id}`,
    );
};
