import * as cartsConstants from '../const/carts';

export const addCart = (product) => {
    return {
        type: cartsConstants.ADD_CART,
        payload: { product },
    };
};

export const addCartSuccess = (cart) => {
    return {
        type: cartsConstants.ADD_CART_SUCCESS,
        payload: { cart },
    };
};

export const updateCart = (product) => {
    return {
        type: cartsConstants.UPDATE_CART,
        payload: {
            product,
        },
    };
};

export const updateCartSuccess = (cart) => {
    return {
        type: cartsConstants.UPDATE_CART_SUCCESS,
        payload: {
            cart,
        },
    };
};
