export const convertPrice = (price) => {
    let leng = price.trim().length;
    var arr = [];
    for (let i = 0; i < leng; ) {
        let text = price.substring(leng - (i + 3), leng - i);
        arr.push(text);
        i += 3;
    }
    var text = '';
    if (arr.length > 0) {
        for (let i = arr.length - 1; i >= 0; i--) {
            text += arr[i] + ' ';
        }
    }
    text = text.trim().replace(/ /g, ',');
    return text;
};
