import React, { useState } from 'react';
import './index.css';
import { convertPrice } from '../../../common/convertPrice';

function DetailProduct(props) {
    const { handleAddCart, product } = props;
    const [quantity, setQuantity] = useState(1);
    const [size, setSize] = useState(50);

    const onPlus = () => {
        setQuantity(quantity + 1);
    };

    const onMinus = () => {
        if (quantity > 1) {
            setQuantity(quantity - 1);
        }
    };

    const selectSize = (e) => {
        if (e.target) {
            setSize(e.target.value);
        }
    };

    return (
        <div className="body3">
            <div className="infor-product">
                <div id="image-product">
                    <div
                        style={
                            product
                                ? {
                                      backgroundImage: `url(${require('../../../img/products/' +
                                          product.img)})`,
                                  }
                                : null
                        }
                    />
                    <div
                        style={
                            product
                                ? {
                                      backgroundImage: `url(${require('../../../img/products/' +
                                          product.img)})`,
                                  }
                                : null
                        }
                    />
                </div>
                <div id="detail-product">
                    <h1>{product ? product.name.toUpperCase() : null}</h1>
                    <p className="line-price">
                        {product ? convertPrice(product.price) : null}
                        <u>đ</u>
                    </p>
                    <div className="product-more-infor">
                        <div className="pro-type">
                            <span>Loại: {product ? product.type : null}</span>
                        </div>
                    </div>
                    <div className="product-infor-grid">
                        <div className="grid__item" />
                    </div>
                    <div className="product-select-watch">
                        <div className="product-select-title">
                            <p>Kích thước:</p>
                        </div>
                        <div className="select-swap">
                            <div className="swatch-element">
                                <input
                                    type="radio"
                                    name="size"
                                    id="size48"
                                    value={48}
                                    onClick={selectSize}
                                />
                                <label
                                    className="size-product"
                                    htmlFor="size48"
                                >
                                    48
                                </label>
                            </div>
                            <div className="swatch-element">
                                <input
                                    type="radio"
                                    name="size"
                                    id="size50"
                                    defaultChecked
                                    value={50}
                                    onClick={selectSize}
                                />
                                <label
                                    className="size-product"
                                    htmlFor="size50"
                                >
                                    50
                                </label>
                            </div>
                            <div className="swatch-element">
                                <input
                                    type="radio"
                                    name="size"
                                    id="size52"
                                    value={52}
                                    onClick={selectSize}
                                />
                                <label
                                    className="size-product"
                                    htmlFor="size52"
                                >
                                    52
                                </label>
                            </div>
                            <div className="swatch-element">
                                <input
                                    type="radio"
                                    name="size"
                                    id="size54"
                                    value={54}
                                    onClick={selectSize}
                                />
                                <label
                                    className="size-product"
                                    htmlFor="size54"
                                >
                                    54
                                </label>
                            </div>
                            <div className="swatch-element">
                                <input
                                    type="radio"
                                    name="size"
                                    id="size56"
                                    value={56}
                                    onClick={selectSize}
                                />
                                <label
                                    className="size-product"
                                    htmlFor="size56"
                                >
                                    56
                                </label>
                            </div>
                        </div>
                    </div>
                    <div className="quantity-addcart">
                        <p>Số lượng:</p>
                        <div className="js-quantity input-group">
                            <form action>
                                <button type="button" onClick={onMinus}>
                                    <i className="fa fa-minus" />
                                </button>
                                <input type="text" value={quantity} />
                                <button type="button" onClick={onPlus}>
                                    <i className="fa fa-plus" />
                                </button>
                            </form>
                        </div>
                    </div>
                    <div className="product-actions">
                        <button
                            type="button"
                            id="add=cart"
                            onClick={() => {
                                handleAddCart(product, quantity, size);
                            }}
                        >
                            <span>THÊM VÀO GIỎ</span>
                        </button>
                        <button type="button" id="buy-now">
                            <span>MUA NGAY</span>
                        </button>
                        {props.children}
                    </div>
                </div>
            </div>
            <div className="related-product" />
        </div>
    );
}

export default DetailProduct;
